
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Loadingscreen } from './../Home-component/loadingscreen/Loadingscreen';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';



export  function Brand() {

    const [allprand ,setallprand] = useState()

    async function getapibarnd (){

        try{

            const {data} =await axios.get('https://route-ecommerce.onrender.com/api/v1/brands')
          
            setallprand(data.data)
            

        }
        catch(e){
            console.log("getapibarnd error",e)
        }

     


    }

useEffect(function(){

    getapibarnd()
    

},[])

    return <>
    <Helmet>
        <title>Brand</title>
    </Helmet>
    
   {allprand ?  <div className=' container'>
        <div className='row g-3'>

            

        {allprand.map(function(pro , idx){return <div key={idx} className='text-center col-md-3'>
               <Link to={`/Brandsdetails/${pro._id}`} className=' text-decoration-none'>
             
               <figure>
                    <img src={pro.image} alt={pro.name} className=' w-100' />
                </figure>
                
                <h4>{pro.name}</h4>
              
                
               </Link>

                 
            </div>})}
        </div>

        



    </div> :<Loadingscreen/>}
    </>
}

