

import React, { useContext, useEffect } from 'react';
import { cartcontext } from '../../Cartcontext/CartProvider';
import { Loadingscreen } from '../Home-component/loadingscreen/Loadingscreen';
import { Helmet } from 'react-helmet';





export function Cart() {
    
    const {totalprice, totalproduct,product,cartproduct ,removeproduct } = useContext(cartcontext)
    //console.log('test pro from cart' ,product)

    useEffect(function(){
      
        cartproduct()
    },[])




   


    return <>
    <Helmet>
        <title>cart</title>
    </Helmet>
    
    
   {product? <div className="container">
   <div className=''>
        <h3>total price:{totalprice}</h3>
        <h3>total product:{totalproduct}</h3>

    </div>
        <div className="row">
       {product.map(function(pro , indx){return<div key={indx} className="col-md-4">
        <figure>
            <img src={pro.product.imageCover} className='w-100' alt={pro.product.title} />
        </figure>
        <h4>title: {pro.product.title}</h4>
        <h4>price: {pro.price } </h4>
        <h4>count:</h4>
        <button onClick={function(){removeproduct(pro.product.id)}} className= 'remove btn btn-danger'>remove </button>

    </div>

       })}
        </div>
    </div>:<Loadingscreen/>}
    </>
}

 