import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Loadingscreen } from '../Home-component/loadingscreen/Loadingscreen';
import { useParams } from 'react-router-dom';

export function Brandsdetails() {
     
const {Brandsdetailsid} =  useParams()

 const [ detailsapi ,setdetailsapi ]  = useState()
    async function Brandsdetailsapi(){
       try {
        const {data} =await axios.get(`https://route-ecommerce.onrender.com/api/v1/products` ,{
        params:{'brand' : `${Brandsdetailsid}`}
       })
       setdetailsapi(data.data)
       console.log(data.data)
       } 
       
       
       catch (error) {
console.log(error)        
       }
      

    }

    useEffect(function(){

        Brandsdetailsapi()
    },[])






    return <>

    {detailsapi?<div className='container ' >

<div className=' row'>

{detailsapi.length===0 ? <h1 className=' text-center'>no product Availble</h1> :detailsapi.map(function(pro , indx){return <div key={indx} className='col-md-3'  >

<figure>
    <img src={pro.imageCover} className='w-100' alt="" />

</figure>
<h5>{pro.title}</h5>

</div>}) }

</div>

</div>:<Loadingscreen/>}
   
    
    </>
}

 