

import React, { useEffect, useState } from 'react';
import {  Navigate, RouterProvider,  createHashRouter } from 'react-router-dom';
import { Layout } from './Component/layout/Layout';
import { Home } from './Component/Home-component/Home';
import { Login } from './Component/login/Login';


import errorimg from '../src/images/error.svg'
import { Registration } from './Component/Regst/registration';
import { Brand } from './Component/Brand/Brand';
import { Prodetails } from './Component/Details/Prodetails';
import { Brandsdetails } from './Component/Brandproduct/Brandsdetails';
import jwtDecode from 'jwt-decode';
import { Profile } from './Component/Profile/Profile';
import { Cart } from './Component/Cart/Cart';
import { CartProvider } from './Cartcontext/CartProvider';







export default function App() {


function Protectedrout ({children}){

  if (userdata == null){
    return <Navigate to='/Login'/>


  }
  else{
    return <>{children}</>

  }




}


  const   [userdata ,setuserdata]  =   useState(null)

  function getuserdata(){

   
    const userdata = jwtDecode(localStorage.getItem('tok'))
   
    setuserdata(userdata);
   
   
    console.log(userdata);
   
   }

   function deleteuserdata(){
    localStorage.removeItem('tok')
    setuserdata(null);

   }

  useEffect(function(){
    if(localStorage.getItem('tok') != null && userdata ==null ){

      getuserdata()

    }


  },[])
  
  const router = createHashRouter([

    
{ path: '' , element:<Layout userdata={userdata } deleteuserdata={deleteuserdata}  /> ,children:[
{path:'Home' , element:<CartProvider><Protectedrout> <Home/>  </Protectedrout></CartProvider> },
{path:'Cart' , element:<CartProvider><Protectedrout> <Cart/>  </Protectedrout></CartProvider> },
{path:'Login' , element:<Login  getuserdata={getuserdata} /> },
{path:'Profile' , element:<Protectedrout><Profile  userdata={userdata} /></Protectedrout> },
{path:'Brandsdetails/:Brandsdetailsid' , element:<CartProvider><Brandsdetails/></CartProvider> },
{path:'Prodetails/:idhome' , element:<CartProvider><Prodetails/></CartProvider> },
{path:'registration' , element:<Registration/> },
{path:'Brand' , element: <CartProvider><Protectedrout><Brand/> </Protectedrout></CartProvider> },


{path:'*' , element:<div className='  d-flex justify-content-center align-items-center'>
  <img src={errorimg} alt="error404" />
  </div>  }




]}


  ])
  
  return <>

  <RouterProvider router={router}/>

</>


  
}

