
import { useFormik } from 'formik';
import React from 'react';

import axios from 'axios';
 import $ from 'jquery'
import { useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet';

export function Registration() {


    const navigate =  useNavigate()

    async function senddata ( obj){
try {
    let {data} = await  axios.post('https://route-ecommerce.onrender.com/api/v1/auth/signup' , obj)

console.log(data)
    if(data.message === "success"){
        $('.success').fadeIn(500 ,function(){
            navigate('/Login')
        });
      
    }
    
} catch (error) {
    //console.log(error.response.data.errors.msg)
    console.log(error)

$('.errord').fadeIn(1000 , function(){


    setTimeout(() => {
        $('.errord').fadeOut(4000)
    }, 1000);
})  



}
       

        

    }

let user ={

 name:"",
 email:"",
 phone :"",
 password:"",
 rePassword:""

}

let formik = useFormik({
    initialValues:user,

    onSubmit: function(values){

        console.log(values)
        senddata(values)

    },

    validate: function(values){
        let errors ={}

        console.log(errors)
        if(values.name.length < 3  || values.name.length > 10) {
            errors.name ="enter valid name";
        }
        if(values.email.includes("@") === false ) {
            errors.email ="Email must contain @";
        }
        if(values.email.includes("gmail.com") === false ) {
            errors.email ="Email must contain gmail.com ";
        }
        if(values.password.length < 5  || values.password.length > 10) {
            errors.password ="enter valid password";
        }
        if(values.password !== values.rePassword ) {
            errors.rePassword ="must be the same ";
        }

       

       



return errors;
    }
});


    return <>

    <Helmet>
        <title>Registration</title>
    </Helmet>


     <h1>registration form</h1>
     
    
        <form onSubmit={formik.handleSubmit}  className=' container py-5'>
     <div className='errord alert alert-danger  ' style={{'textAlign' : 'center' , 'display':'none' }}>emai.l klhkgou</div> 
           

            <label className='mt-3' htmlFor="name">name</label>
            <input  onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.name} type="text" id='name' placeholder='name..' className=' form-control' />
             {formik.errors.name && formik.touched.name? <div className=' alert alert-danger  '>{formik.errors.name}</div>     : ""}
            
            
            
            <label className='mt-3' htmlFor="email">email</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.email} type="email" id='email' placeholder='email..' className=' form-control' />
           
            {formik.errors.email && formik.touched.email ?  <div className=' alert alert-danger  '>{formik.errors.email}</div>     : ' '}
            
            <label className='mt-3' htmlFor="phone">phone</label>
            <input  onChange={formik.handleChange}   value={formik.values.phone} type="number" id='phone' placeholder='phone..' className=' form-control' />
           

            
            
            
            <label className='mt-3' htmlFor="password">password</label>
            <input  onChange={formik.handleChange} onBlur={formik.handleBlur}  value={formik.values.password} type="password" id='password' placeholder='password..' className=' form-control' />
            
            {formik.errors.password  && formik.touched.password?   <div className=' alert alert-danger  '>{formik.errors.password}</div>     : " "}




            
            <label className='mt-3' htmlFor="rePassword">rePassword</label>
            <input  onChange={formik.handleChange}  onBlur={formik.handleBlur} value={formik.values.rePassword} type="password" id='rePassword' placeholder='rePassword..' className=' form-control' />
           
            {formik.errors.rePassword && formik.touched.rePassword ?    <div className=' alert alert-danger  '>{formik.errors.rePassword}</div>     : " "}

            
            <button type='submit' className='btn btn-outline-primary mt-3'>rePister</button>
            <div className='success alert alert-success  mt-2 fs-3' style={{'textAlign' : 'center' , 'display':' none' }}>success</div> 

        </form>
    
    
    
    </>
}
