

import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import logo from '../../images/freshcart-logo.svg'
export function Navbar({userdata ,deleteuserdata}) {
  const navigat = useNavigate() 
  
  function clearuser(){
    deleteuserdata()
    navigat('/Login')

  }
    return <>
    <nav className="navbar navbar-expand-lg  bg-light">
  <div className="container">
    <Link className="navbar-brand" to="/Home"><img src={logo} alt="logo" /></Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/Home">Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/Brand">Brand</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/Cart">Cart</Link>
        </li>
      </ul>
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0 d-flex align-items-center">
      
{userdata ? <><li className="nav-item">
          <span  onClick={clearuser} className="logoutspan   nav-span active" aria-current="page">Logout</span>
        </li>
      <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/Profile">Profile</Link>
        </li></> :<>
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/Login">Login</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/registration">registration</Link>
        </li></>}


        
      </ul>
      
    </div>
  </div>
</nav>
    
    </>
}
