

 import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react';
import { Loadingscreen } from './loadingscreen/Loadingscreen';
import { Link } from 'react-router-dom';
import { MySlider } from '../Slider/MySlider';
import { cartcontext } from './../../Cartcontext/CartProvider';
import  $ from  'jquery'
 import { Helmet } from 'react-helmet';

 export function Home() {  

    const  {addcart ,removeproduct}= useContext(cartcontext)
    async function addmyproduct(id ,idx){
        if ( await addcart(id) === true){ 
            addcart(id)
            $(`#add${idx}`).fadeOut(1000)
            $(`#remove${idx}`).fadeIn(1000)
            $('.successmsg').fadeIn(1000 ,function(){
                setTimeout(() => {
                    $('.successmsg').fadeOut(2000)
                }, 3000);
            })
            

        }
    }

    async function removemyproduct(id ,idx){
        if ( await removeproduct(id) === true){ 
            removeproduct(id)
            $(`#remove${idx}`).fadeOut(1000)
            $(`#add${idx}`).fadeIn(1000)
            $('.removemsg').fadeIn(1000 ,function(){
                setTimeout(() => {
                    $('.removemsg').fadeOut(2000)
                }, 3000);
            })
            

        }
    }

    

const  [allproduct , setallproduct]=  useState(null)


    async function getallproductapi(){
try{
    
        const {data} = await axios.get(`https://route-ecommerce.onrender.com/api/v1/products`,{
            params:{'sort': 'title'}
        })
     
      setallproduct(data.data)
      console.log(data)
     
}
catch(e){
    console.log( "rerer", e)
}
    }


useEffect(function(){

    getallproductapi()
},[])


    return <>
<Helmet>
    <title>Home</title>
</Helmet>
    
    
{allproduct? <div className="container">
<MySlider/>

<div style={{'zIndex':'700' , 'left':'0' , "display" : 'none'} }  className=  'successmsg  alert  alert-success bottom-0  text-center  position-fixed'>product added succssfully</div>
<div style={{'zIndex':'700' , 'left':'0' , "display" : 'none'} }  className=  'removemsg  alert  alert-danger bottom-0  text-center  position-fixed'>product removed succssfully</div>
    <div className=' row g-3 mt-5'>
       
       {allproduct.map( function( pro , idx) {return  <div className= '  col-md-2 ' key={idx}>


        <div className=' up'>
        <Link to={`/Prodetails/${pro._id}`} className='  text-dark text-decoration-none'>
           <div className='  rounded-3  position-relative' >
           <figure className=' m-0 p-0'>
                <img src={pro.imageCover} alt={pro.title}  className='w-100 '/>
            </figure>
            
          
           <div className=' position-absolute top-0 end-0 bg-info'>
            <h5>{pro.ratingsAverage}</h5>

           </div>
           </div>
           
           </Link>
        </div>
        
        <div className='p-3 bg-primary rounded-3 '>
           <h6>title: {pro.title.slice(0, pro.title.indexOf( ' ' , 5))} </h6>
            <h5>name:{pro.brand.name}</h5>
            <h5>price:{pro.priceAfterDiscount? <>
            <span className='text-decoration-line-through '>{pro.price}</span>
            <span className=' ms-2'>{pro.priceAfterDiscount}</span>
            </>:  <span>{ pro.price}</span> }</h5> 
            <button id={`add${idx}`} onClick={function(){addmyproduct(pro._id ,idx)}} className= 'add btn btn-info'>add to cart</button>
            <button onClick={function(){removemyproduct(pro._id ,idx)}}  id={`remove${idx}`} style={{'display' :'none' }}  className= 'remove btn btn-danger'>remove to cart</button>

           </div>
            
        
           

          
           


        </div>

        


       }) }
        

    </div>
    </div>  :<Loadingscreen/>}


  
    
    </>
}
