

import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react';
import { Loadingscreen } from '../Home-component/loadingscreen/Loadingscreen';
import { useParams } from 'react-router-dom';
import { cartcontext } from './../../Cartcontext/CartProvider';
import $ from 'jquery'; 

export  function Prodetails() {

const {addcart ,removeproduct}= useContext(cartcontext)

async function testaddcart (id){
    if(await addcart(id) === true){
       
        $('.add').fadeOut()
        $('.remove').fadeIn()
        addcart(id)
        $('.successmsg').fadeIn(1000 ,function(){
            setTimeout(() => {
                $('.successmsg').fadeOut(2000)
            }, 3000);
        })
        
        
    }

}
async function testremovecart (id){
   
    if(await removeproduct(id) === true){
       
        removeproduct(id)
        $('.remove').fadeOut()
        $('.add').fadeIn()
       
        $('.removemsg').fadeIn(1000 ,function(){
            setTimeout(() => {
                $('.removemsg').fadeOut(2000)
            }, 3000);
        })
        
        
    }

}



    const {idhome }= useParams()

console.log(idhome)



 const[Details , setDetails] = useState()

   async function getapiProdetails(){
try {
    const{data}= await axios.get(`https://route-ecommerce.onrender.com/api/v1/products/${idhome}`)
//console.log(data.data)

setDetails(data.data)


} catch (error) {
    console.log(error)
    
}}

useEffect(function (){
    getapiProdetails()

},[])



    return <>
   

   {Details? <div className=' container'>

<div className=' row justify-content-center align-items-center'>

    <div className=' col-md-3'>
        <figure>
            <img src={Details.imageCover} alt={Details.name} className=' w-100' />
        </figure>
    </div>
    <div className=' col-md-9 '>
        <h4 >{Details.title}</h4>
        <p >{Details.description}</p>
<h5>Price: {Details.price}</h5>
<h5>quantity: {Details.quantity}</h5>

<button  onClick={function(){testaddcart(Details.id)}}  className=' btn btn-outline-success w-100 add'>add to cart +</button>
<button onClick={function(){testremovecart(Details.id)}} style={{'display' : 'none' }}  className=' btn btn-outline-danger w-100 remove'>removed from cart -</button>
<div style={{'display': 'none'}} className="alert alert-success text-center successmsg">
    added to cart
</div>
<div style={{'display': 'none'}} className="alert alert-danger text-center removemsg">
    remove from cart
</div>
    </div>

</div>

</div>: <Loadingscreen/>}
    
    </>
}





